import api from './api';
import { CreateRequestPayload } from '@shared/types';

export const requestsApi = {
  // NGO actions
  create: async (payload: CreateRequestPayload) => {
    const res = await api.post('/requests', payload);
    return res.data;
  },
  getNGORequests: async (status?: string, page = 1, limit = 10) => {
    const params = new URLSearchParams();
    if (status && status !== 'all') params.append('status', status);
    params.append('page', page.toString());
    params.append('limit', limit.toString());
    const res = await api.get(`/requests/ngo?${params}`);
    return res.data;
  },

  // Hotel actions
  getHotelRequests: async (status?: string, page = 1, limit = 10) => {
    const params = new URLSearchParams();
    if (status && status !== 'all') params.append('status', status);
    params.append('page', page.toString());
    params.append('limit', limit.toString());
    const res = await api.get(`/requests/hotel?${params}`);
    return res.data;
  },
  updateStatus: async (id: string, status: 'accepted' | 'rejected') => {
    const res = await api.patch(`/requests/${id}/status`, { status });
    return res.data;
  },
};
