'use client';

import React, { useEffect, useState } from 'react';
import { requestsApi } from '@/lib/requests';
import { RequestItem } from '@shared/types';
import { Clock, CheckCircle, XCircle, Phone, User2, UtensilsCrossed } from 'lucide-react';

const HotelRequestsPage: React.FC = () => {
  const [loading, setLoading] = useState(true);
  const [statusFilter, setStatusFilter] = useState('pending');
  const [requests, setRequests] = useState<RequestItem[]>([]);
  const [updatingId, setUpdatingId] = useState<string | null>(null);

  const load = async () => {
    try {
      setLoading(true);
      const res = await requestsApi.getHotelRequests(statusFilter);
      setRequests(res.requests || []);
    } catch (e) {
      console.error('Failed to load hotel requests', e);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    load();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [statusFilter]);

  const updateStatus = async (id: string, status: 'accepted' | 'rejected') => {
    try {
      setUpdatingId(id);
      await requestsApi.updateStatus(id, status);
      await load();
    } catch (e) {
      console.error('Failed to update request status', e);
      alert('Failed to update request');
    } finally {
      setUpdatingId(null);
    }
  };

  const getStatusPill = (status: string) => {
    const base = 'inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium';
    switch (status) {
      case 'pending':
        return <span className={`${base} bg-yellow-100 text-yellow-800`}><Clock className="h-3 w-3 mr-1"/>Pending</span>;
      case 'accepted':
        return <span className={`${base} bg-green-100 text-green-800`}><CheckCircle className="h-3 w-3 mr-1"/>Accepted</span>;
      case 'rejected':
        return <span className={`${base} bg-red-100 text-red-800`}><XCircle className="h-3 w-3 mr-1"/>Rejected</span>;
      default:
        return <span className={`${base} bg-gray-100 text-gray-800`}>{status}</span>;
    }
  };

  if (loading) {
    return (
      <div className="max-w-7xl mx-auto py-6 px-4 sm:px-6 lg:px-8">
        <div className="text-center">Loading incoming requests...</div>
      </div>
    );
  }

  return (
    <div className="max-w-7xl mx-auto py-6 px-4 sm:px-6 lg:px-8">
      <div className="mb-8 flex flex-col sm:flex-row gap-4 justify-between items-start sm:items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Incoming Requests</h1>
          <p className="mt-2 text-gray-600">Review and accept or reject NGO requests</p>
        </div>
        <div>
          <select
            value={statusFilter}
            onChange={(e) => setStatusFilter(e.target.value)}
            className="rounded-md border-gray-300 shadow-sm focus:border-green-500 focus:ring-green-500"
          >
            <option value="pending">Pending</option>
            <option value="accepted">Accepted</option>
            <option value="rejected">Rejected</option>
            <option value="all">All</option>
          </select>
        </div>
      </div>

      {requests.length === 0 ? (
        <div className="text-center py-12">
          <p className="text-gray-500">No requests found.</p>
        </div>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {requests.map((req) => (
            <div key={req._id} className="bg-white rounded-lg shadow-md p-6">
              <div className="flex items-start justify-between">
                <h3 className="text-lg font-semibold text-gray-900">{req.title}</h3>
                {getStatusPill(req.status)}
              </div>
              <p className="text-sm text-gray-600 mt-2 line-clamp-2">{req.description}</p>

              <div className="mt-4 space-y-2 text-sm text-gray-700">
                <div className="flex items-center">
                  <UtensilsCrossed className="h-4 w-4 text-gray-500 mr-2"/>
                  <span>{req.foodType} • {req.quantity.amount} {req.quantity.unit}</span>
                </div>
                <div className="flex items-center">
                  <User2 className="h-4 w-4 text-gray-500 mr-2"/>
                  <span>{req.ngoRequester.organizationName} • {req.ngoRequester.contactPerson}</span>
                </div>
                <div className="flex items-center">
                  <Phone className="h-4 w-4 text-gray-500 mr-2"/>
                  <span>{req.ngoRequester.phone}</span>
                </div>
              </div>

              {req.status === 'pending' && (
                <div className="mt-4 grid grid-cols-2 gap-2">
                  <button
                    disabled={updatingId === req._id}
                    onClick={() => updateStatus(req._id, 'accepted')}
                    className="px-3 py-2 rounded-md text-sm font-medium text-white bg-green-600 hover:bg-green-700 disabled:opacity-50"
                  >
                    {updatingId === req._id ? 'Saving...' : 'Accept'}
                  </button>
                  <button
                    disabled={updatingId === req._id}
                    onClick={() => updateStatus(req._id, 'rejected')}
                    className="px-3 py-2 rounded-md text-sm font-medium text-red-700 bg-white border border-red-300 hover:bg-red-50 disabled:opacity-50"
                  >
                    Reject
                  </button>
                </div>
              )}

              <div className="mt-4 text-xs text-gray-500">
                <p>Requested on {new Date(req.createdAt).toLocaleString()}</p>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default HotelRequestsPage;
